.case {
  width: 100%;
  margin: auto;
  margin-bottom: 100px;

  @media (max-width: 1199px) {
    margin-bottom: 50px;
  }

  @media (max-width: 719px) {
    margin-bottom: 30px;
  }
}

.case__header {
  display: flex;
  border-bottom: 3px solid #000000;
  font-family: "Suisse Intl";
  cursor: pointer;
  transition: all 0.1s ease-out;
  padding: 0 1%;

  @media (max-width: 1023px) {
    width: 95%;
    padding: 0 2.5%;
  }

  h2 {
    margin: 0;
    text-transform: uppercase;
    font-style: normal;
    font-weight: 400;
    font-size: 80px;
    line-height: 90%;
    width: 41%;
    margin-top: 26px;

    @media (max-width: 1439px) {
      font-size: 35px;
      margin-top: 28px;
    }

    @media (max-width: 719px) {
      font-size: 25px;
      margin-top: 20px;
      width: 50%;
      line-height: 100%;
    }
  }

  p {
    margin: 0;
    margin-top: 75px;
    padding: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    width: 35%;

    @media (max-width: 1439px) {
      font-size: 16px;
      margin-top: 30px;
    }

    @media (max-width: 719px) {
      font-size: 14px;
      width: 25%;
      margin-top: 29px;
      line-height: 100%;
    }

    &:first-of-type {
      width: 20%;

      @media (max-width: 1023px) {
        width: 25%;
      }

      @media (max-width: 719px) {
        width: 30%;
      }
    }
  }
}

.case__active {
  overflow: hidden;
  max-height: 0px;
  transition: max-height 0.1s ease-out;
  display: flex;
  justify-content: space-between;
  width: 98%;
  margin: auto;
  margin-top: 50px;
  box-sizing: content-box;

  @media (max-width: 1439px) {
    flex-direction: column;
    margin-top: 35px;
  }

  @media (max-width: 1023px) {
    width: 95%;
  }

  @media (max-width: 719px) {
    margin-top: 20px;
  }

  p {
    width: 39%;
    font-family: "Suisse Intl";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    margin: 0;

    @media (max-width: 1439px) {
      width: 65%;
      margin-bottom: 35px;
      font-size: 16px;
    }

    @media (max-width: 1023px) {
      width: 86%;
    }

    @media (max-width: 719px) {
      width: 100%;
      font-size: 14px;
    }
  }

  img {
    width: 59%;
    @media (max-width: 1439px) {
      width: 80%;
    }

    @media (max-width: 1023px) {
      width: 100%;
    }
  }

  div {
    @media (max-width: 499px) {
      display: flex;

      p {
        margin-bottom: 20px;
      }
    }
  }
}

.case__active[aria-expanded="true"] {
  max-height: 1000px;
}
