.herobanner {
  top: 0;
  width: 100vw;
  height: 100vh;
  min-height: 650px;
  max-height: 950px;
  margin: auto;
  margin-bottom: 77px;
  cursor: pointer;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @media (max-width: 1439px) {
    min-height: 500px;
    max-height: 850px;
  }

  @media (max-width: 499px) {
    margin-bottom: 30px;
    height: 450px;
  }
}

.background_0 {
  background-image: url(../../img/bg1_1920.png);

  @media (max-width: 499px) {
    background-image: url(../../img/bg1_320.png);
  }
}

.background_1 {
  background-image: url(../../img/bg2_1920.png);

  @media (max-width: 499px) {
    background-image: url(../../img/bg2_320.png);
  }
}

.background_2 {
  background-image: url(../../img/bg3_1920.png);

  @media (max-width: 499px) {
    background-image: url(../../img/bg3_320.png);
  }
}

.background_3 {
  background-image: url(../../img/bg4_1920.png);

  @media (max-width: 719px) {
    background-image: url(../../img/bg4_500.png);
  }

  // @media (max-width: 1439px) {
  //   background-image: url(../../img/bg4_1440.png);
  // }
}

.background_4 {
  background-color: lightseagreen;
}
